//colors
$background: #e4dfd3;
$green: #344f10;
$swamp: #8C9772;
$dark: #333333;
$beige: #E4DFD3;
$whitish: #F2F2F2;

//breackpoints

$smallLaptop: 1050px;
$tablet: 800px;
$phone: 450px;


$hoverDuration: 0.2s;

//animations
@keyframes swing {
  15% {
    transform: translateX(5px);
  }
  30% {
    transform: translateX(-5px);
  }
  50% {
    transform: translateX(3px);
  }
  65% {
    transform: translateX(-3px);
  }
  80% {
    transform: translateX(2px);
  }
  100% {
    transform: translateX(0);
  }
}


@keyframes shadow-pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.2);
  }
  100% {
    box-shadow: 0 0 0 45px rgba(0, 0, 0, 0);
  }
}
